import { Bar, mixins } from 'vue-chartjs';
const { reactiveProp } = mixins;

export default {
	extends: Bar,
	mixins: [reactiveProp],
	props: {
		chartData: {
			type: Object,
			default: null,
		},
		options: {
			type: Object,
			default: null,
		},
	},
	// props: ['chart-date', 'options'],

	mounted() {
		// this.chartData is created in the mixin.
		// If you want to pass options please create a local options object
		this.renderChart(this.chartData, this.options, {
			scaleOverride: true,
			scaleSteps: 10,
			scaleStepWidth: 50,
			scaleStartValue: 10,

			// scaleEndValue: 3000,
		});
	},
};
